/* BODY */

body {
    font-family: 'Montserrat', sans-serif;
}
h1, h2, h3, h4, h5, h6 {
    font-family: 'Roboto', sans-serif;
    text-align: center;
}
h1 {
    font-weight: 700;
}

/* NAVBAR */

#navigationBar {
    background-color: RGB(116, 0, 184);
}

/* MAIN */

main {
    padding-bottom: 30px;
}

/* HOME */
.home-portfolio-example {
    max-width: 100%;
    height: auto;
    border: 1px solid RGB(116, 0, 184);
}
.home-links, .home-links:link, .home-links:visited, .home-links:active {
    align-self: center;
    padding: 10px;
    background-color: RGB(116, 0, 184);
    color: white;
    border-radius: 16px;
    font-weight: 700;
    text-decoration: none;
    border: 1px solid white;
}
    .home-links:hover {
        background-color: white;
        color: RGB(116, 0, 184);
        border: 1px solid RGB(116, 0, 184);
        text-decoration: none;
    }
.home-section {
    padding-top: 15px;
    padding-bottom: 15px;
}
.home-midsection {
    border-top: 2px solid RGB(116, 0, 184);
    border-bottom: 2px solid RGB(116, 0, 184);
}
/* ABOUT */

.subsection-container {
    display: flex;
    justify-content: space-evenly;
}
.subsection-container-50PER {
    flex-basis: 48%;
}
#photo-dan {
    max-width: 100%;
    height: auto;
    border: 1px solid RGB(116, 0, 184);
}
.about-experience-heading {
    padding: 5px;
    margin: auto;
    color: RGB(116, 0, 184);
}
.about-scroll {
    font-size: 1.2rem;
    padding: 3px;
}
.download-cv, .download-cv:link, .download-cv:visited, .download-cv:active {
    padding: 2px;
    background-color: RGB(116, 0, 184);
    color: white;
    border-radius: 5px;
    font-weight: 700;
    text-decoration: none;
    border: 1px solid white;
}
    .download-cv:hover {
        background-color: white;
        color: RGB(116, 0, 184);
        border: 1px solid RGB(116, 0, 184);
        text-decoration: none;
    }

/* PORTFOLIO */

.mobile-app-container {
    padding-top: 10px;
    padding-bottom: 10px;
    border-top: 2px solid RGB(116, 0, 184);
}
.mobile-app {
    height: auto;
    border: 1px solid RGB(116, 0, 184);
}
.badge {
    margin-right: 2px;
}

/* CONTACT */

.contact-container {
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-top: 2px solid RGB(116, 0, 184);

}
/* FOOTER */

#footer {
    position: fixed;
    left: 0;
    bottom: 0;
    text-align: center;
    width: 100%;
    background-color: RGB(116, 0, 184);
    color: white;
}
#footer p {
    margin: 0;
    padding: 10px;
    font-style: italic;
}